export default {
	data() {
		return {
			arr:[],
			useUser:this.user,
			url:this.url1
		}
	},
	onLoad(options) {
	    this.name = options.name;
		this.getMy()
	},
	methods: {
		getMy:function(){
			uni.showLoading({
				title:'正在加载',mask: true
			})
			let that=this;
			let usurl=that.url1+"/dinghai/Posted/selectMyPosted";
			uni.request({
					url: usurl,
					header:{
						"Content-Type" : "application/x-www-form-urlencoded"
					},
					method:'POST',
					data: {
						uid:that.useUser.uid
					},
			    success: (res) => {
					console.log(res)
					if(res.statusCode==200){
						for(let i=0;i<res.data.flag.length;i++){
							res.data.flag[i].postedImg=JSON.parse(res.data.flag[i].postedImg)
						}
						that.arr=res.data.flag;
						
						uni.showToast({
							title:"加载成功"
						})
					}else{
						uni.showToast({
							title:"您的网络打了个盹",
							icon:"none"
						})
					}
			    },
				fail : function(err){
					uni.showToast({
						title:"您的网络打了个盹",
						icon:"none"
					})
				}
			});
		},
		back : function(){
		   uni.navigateBack({
		
		   });
		},
		tiao:function(item){
			uni.navigateTo({
				url:'../../post/pingJia/pingJia?id='+JSON.stringify(item)
			})
		}
	}
}
